export class Entity {
    id;
    #x;
    #y;
    #z;
    width = 0;
    height = 0;
    constructor(x, y){
        this.id = crypto.randomUUID();
        this.#x = x;
        this.#y = y;
        this.#z = 1;
    }
    set x(x) {
        this.#x = x;
    }
    get x() {
        return this.#x;
    }
    set z(z) {
        this.#z = z;
    }
    get z() {
        return this.#z;
    }
    set y(y) {
        this.#y = y;
    }
    get y() {
        return this.#y;
    }
    setPosition(x, y, z) {
        this.#x = x;
        this.#y = y;
        if (z) this.#z = z;
    }
}
