import { Entity } from "../../../mod.ts";
export class Group extends Entity {
    children = [];
    scene;
    constructor(scene, x, y){
        super(x, y);
        this.scene = scene;
    }
    addChild(child) {
        this.children.push(child);
    }
    killChild(child) {
        this.children.splice(this.children.indexOf(child), 1);
    }
    killAllChildren() {
        this.children = [];
    }
}
