import { Entity } from "../mod.ts";
import { atlas } from "./../../utils/atlas/mod.ts";
export class Atlas extends Entity {
    atlas;
    // @ts-ignore: typescript is weird
    bitmap;
    // @ts-ignore: typescript is weird
    image;
    preloaded = false;
    constructor(// deno-lint-ignore no-explicit-any
    _atlas, type = "phaser"){
        super(0, 0);
        // @ts-ignore: typescript is weird
        this.image = document.createElement("img");
        this.atlas = atlas(_atlas, type);
    }
    getFrame(key) {
        return this.atlas.frames[key];
    }
    get imgUrl() {
        return this.atlas.imgUrl;
    }
    load() {
        return new Promise((res, rej)=>{
            this.image.src = this.atlas.imgUrl;
            this.image.onload = ()=>{
                this.width = this.image.width;
                this.height = this.image.height;
                this.preloaded = true;
                // @ts-ignore: typescript is weird
                createImageBitmap(this.image).then((img)=>{
                    this.bitmap = img;
                    res(this);
                });
            };
            this.image.onerror = rej;
        });
    }
}
