import { GodotAtlas, PhaserAtlas, PixiAtlas } from "./mod.ts";
export function atlas(// deno-lint-ignore no-explicit-any
data, type = "phaser") {
    let atlas1;
    switch(type){
        case "phaser":
            {
                atlas1 = new PhaserAtlas(data);
                break;
            }
        case "pixi":
            {
                atlas1 = new PixiAtlas(data);
                break;
            }
        case "godot":
            {
                atlas1 = new GodotAtlas(data);
                break;
            }
        default:
            {
                throw new Error(`Unknown atlas type: ${type}`);
            }
    }
    return atlas1;
}
