import { AtlasSprite, Image } from "../../mod.ts";
export class Scene {
    world;
    entities = [];
    #resources = [];
    resources = [];
    constructor(world){
        this.world = world;
    }
    async loadResources() {
        await Promise.all(this.#resources);
    }
    setKeys(_keys) {
        this.world.keyManager.setKeys(_keys);
    }
    addChild(e) {
        if (e instanceof Array) {
            this.entities.push(...e);
            for (const entity of e){
                if (entity instanceof Image || entity instanceof AtlasSprite /*|| entity instanceof AtlasSprite */ ) {
                    this.#resources.push(entity.load());
                }
            }
        } else {
            this.entities.push(e);
            if (e instanceof Image || e instanceof AtlasSprite /*|| e instanceof Sprite*/ ) {
                this.#resources.push(e.load());
            }
        }
    }
    killChild(e) {
        const index = this.entities.indexOf(e);
        if (index < -1) return;
        this.entities.splice(index, 1);
    }
    // deno-lint-ignore no-explicit-any
    loadPlugin(name, plugin) {
        this.world.loadPlugin(name, plugin);
    }
    usePlugin(name) {
        return this.world.usePlugin(name);
    }
    // deno-lint-ignore no-explicit-any
    _mouseDown(e) {
        // deno-lint-ignore no-unused-vars
        for (const entity of this.entities){
        //   if (entity instanceof Button) {
        //     if (
        //       e.x >= entity.x &&
        //       e.x <= entity.child.x + entity.child.width &&
        //       e.y >= entity.child.y &&
        //       e.y <= entity.child.y + entity.child.height
        //     ) {
        //       entity.onClick();
        //     }
        //   }
        }
        this.mouseDown(e);
    }
    // deno-lint-ignore no-explicit-any
    _mouseMotion(e) {
        this.mouseMotion(e);
    }
    setBackground(color) {
        this.world.renderer.setBackground(color);
    }
    tick() {}
    // deno-lint-ignore no-explicit-any
    mouseDown(_e) {}
    // deno-lint-ignore no-explicit-any
    mouseMotion(_e) {}
    setup() {}
    update() {}
    keyDown(e) {
        return this.world.keyManager.isDown(e);
    }
}
