export class PixiAtlas {
    frames = {};
    width;
    height;
    imgUrl;
    constructor(data){
        this.width = data.meta.size.w;
        this.height = data.meta.size.h;
        this.imgUrl = data.meta.image;
        for(const i in data.frames){
            const frame = data.frames[i];
            this.frames[i] = {
                x: frame.frame.x,
                y: frame.frame.y,
                width: frame.frame.w,
                height: frame.frame.h
            };
        }
    }
}
